.CartForm input{
  display:block;
  padding-left:2%;
}
.CartForm{
  background-color: #E4E4E1;
  background-image: linear-gradient(to top, #e6b980 0%, #eacda3 100%);
  color:black;
  padding:10%;
  border-radius:10px;
  height:40vh;
  margin-right:8%;
  width:17rem;
  font-weight:600;
  border:3px solid orange;
}
.CartForm input{
   outline: none;
   border:2px solid orange;
   height:5vh;
   width:100%;
   border-radius: 5px;
   margin-top:2%;
   margin-bottom:5%;
   font-size: medium;
}
button{
  background-image: linear-gradient(-225deg, #231557 0%, #44107A 29%, #FF1361 67%, #FFF800 100%);
  color:white;
  border:none;
  height:6vh;
  width:6rem;
  border-radius: 5px;
  font-size:15px;
  font-weight:600;
  color:rgb(160, 248, 29);
}
.App h2{
  color:orange;
  font-weight: 800;
  font-size: 40px;
  text-align: center;
  padding-bottom:1%;
}
.App .ProductList {
  display : flex;
  flex-direction : row;
  justify-content: space-around;              
}
tbody{
  color:rgb(255, 197, 88);
}
th,td{
  width:35vh;
  padding:1%;
  
}
td{
  text-align: center;
  color:white;
}
table{
  border:1px solid white;
  background-color: rgb(75, 75, 75) ;
  border-radius:10px;
}
.Cart{
  margin-right:2%;
}
.MainCart{
  margin-top:-4%;
  margin-right:3%;
}
.MainCart h1{
  color:orange;
  font-size: 25px;
  font-weight:500;
  text-align: center;
}
.row td input[type = "button"]{
  background-color: rgb(0, 0, 0) ;
  color:white;
  border-radius:2px;
  outline: none;
  border:none;
  margin:2%;
}



